.cmnt_table {
  margin: 0 0 35px;
  width: 100%;
  text-align: center;
  border-collapse: collapse;
  background: #fff;
  colgroup {
    .number {
      width: 7.5%;
    }
    .title {
      width: 50%;
    }
    .user {
      width: 20%;
    }
    .date {
      width: 15%;
    }
    .rmd {
      width: 7.5%;
    }
  }
  thead {
    th {
      background: #f9f9f9;
      height: 30px;
      color: #999;
    }
  }
  tbody {
    tr {
      height: 40px;
      &:hover {
        background: #f9f9f9;
      }
      td {
        border-top: 1px solid #d9d9d9;
        border-bottom: 1px solid #d9d9d9;
        color: #666;
        &:nth-child(2) {
          font-size: 0.9rem;
          padding-left: 2rem;
          text-align: left;
        }
        a {
          display: block;
          padding: 10px;
        }
      }
    }
  }
}
