.comment_list {
  margin-top: 20px;
  li {
    position: relative;
    padding: 20px 10px;
    border-top: 1px solid #e9e9e9;
    border-bottom: 1px solid #e9e9e9;

    color: #fff;
    span {
      padding-right: 10px;
    }
    .date {
      font-size: 14px;
    }
    p {
      padding: 10px 0;
    }
    button {
      padding: 10px;
      color: #8b8b8b;
    }
    button:hover {
      color: #222;
      text-decoration: underline;
    }
    .remove {
      position: absolute;
      top: 10px;
      right: 10px;
    }
    .edit {
      position: absolute;
      bottom: 10px;
      right: 10px;
    }
  }
}
